import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import { Account, Address } from 'src/app/objects/account.model';
import { AccountService } from 'src/app/services/account.service';
import { AuthService} from 'src/app/services/auth.service'
import { LoadingController } from '@ionic/angular';
import { Router } from '@angular/router';
import * as firebase from 'firebase';
import { FileService } from 'src/app/services/file.service';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import {CityTown}  from 'src/app/objects/city-town.model'; 

@Component({
  selector: 'app-account',
  templateUrl: './account.page.html',
  styleUrls: ['./account.page.scss'],
})
export class AccountPage implements OnInit {
  account:Account   = new Account(); 
  tag_exist: number = 2;
  checking: boolean = false;
  imgInputElm: HTMLInputElement;
  profilPhotoBase64: string;
  iller :any        =[];
  ilceler: any[];
  addresses: Address[];
  editedAddress: Address;
  @ViewChild('content') private content: any;

  constructor(public location:Location, 
              public authService:AuthService,
              public router:Router,
              private fileService:FileService,
              private loadingController: LoadingController,
              private accountService:AccountService){}

  async ngOnInit() { 
    this.iller    = (new CityTown()).getCityes(); 
  } 

  async ionViewWillEnter(){ 
    if(this.authService.USER === undefined ){
      this.router.navigate(['/']);
      return;      
    } 
    this.getAccount() 
    this.getAddresses() 
  }

  async getAccount(){ 
    let uid = this.authService.USER.uid;
    this.accountService.getAccount(uid).then(async (data)=>{
      if(data){  
        this.account = data;
      }
    })
  }

  getAddresses(){
    let uid = this.authService.USER.uid;
    this.accountService.getAddresses(uid).valueChanges({idField:'id'}).subscribe(async (datas)=>{ 
      this.addresses = datas.map(doc=>{ 
        return {id:doc.id,...doc}
      })
    })
  }

  saveAdress(){

    let error:boolean = false;
    document.getElementById('name').style.border        = "1px solid #ccc";
    document.getElementById('adi_soyadi').style.border  = "1px solid #ccc";
    document.getElementById('tel').style.border         = "1px solid #ccc";
    document.getElementById('mahalle').style.border     = "1px solid #ccc";
    document.getElementById('cadde_sokak').style.border = "1px solid #ccc";
    document.getElementById('bina').style.border        = "1px solid #ccc";

    if(this.editedAddress.name?.trim() =='' || this.editedAddress.name === undefined){
      document.getElementById('name').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.adi_soyadi?.trim() == '' || this.editedAddress.adi_soyadi === undefined){
      document.getElementById('adi_soyadi').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.tel?.trim() == '' || this.editedAddress.tel === undefined){
      document.getElementById('tel').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.mahalle?.trim() == '' || this.editedAddress.mahalle === undefined){
      document.getElementById('mahalle').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.cadde_sokak?.trim() == '' || this.editedAddress.cadde_sokak === undefined){
      document.getElementById('cadde_sokak').style.border = "1px solid red";
      error = true
    }
    
    if(this.editedAddress.bina?.trim() == '' || this.editedAddress.bina === undefined){
      document.getElementById('bina').style.border = "1px solid red";
      error = true
    }

    if(error){return;}

    let uid = this.authService.USER.uid; 
    this.accountService.saveAddress(uid,this.editedAddress).then(()=>{
      setTimeout(() => { 
        this.editedAddress = this.addresses[this.addresses.length-1];
      }, 500);
    })
  }

  deleteAdress(){
    if(!this.editedAddress.id){
      this.editedAddress = null;
      return;
    }
    
    let uid = this.authService.USER.uid;
    this.accountService.deleteAdress(uid,this.editedAddress).then(()=>{
      if(this.addresses[0]){
        this.switchAddress(this.addresses[0])
      }else{
        this.newAdress();
      }
    })

  }

  switchAddress(address:Address){ 

    try{
      document.getElementById('name').style.border        = "1px solid #ccc";
      document.getElementById('adi_soyadi').style.border  = "1px solid #ccc";
      document.getElementById('tel').style.border         = "1px solid #ccc";
      document.getElementById('mahalle').style.border     = "1px solid #ccc";
      document.getElementById('cadde_sokak').style.border = "1px solid #ccc";
      document.getElementById('bina').style.border        = "1px solid #ccc";
    }catch (error) { }

    this.editedAddress = address
    this.ilceler  = (new CityTown()).getTowns(this.editedAddress.city['id']);
    let yOffset = document.getElementById("anchor").offsetTop ;
    this.content.scrollToPoint(0, yOffset, 1000)
  }

  newAdress(){ 
    this.editedAddress = new Address();
    let yOffset = document.getElementById("anchor").offsetTop ;
    this.content.scrollToPoint(0, yOffset, 1000)
  }

  changeCtity(event:any){
    this.ilceler  = (new CityTown()).getTowns(this.editedAddress.city['id']);    
    this.iller.map(il=>{
      if(event.target.value == il.id){
        this.editedAddress.il_adi  = il.name;
      } 
    })
  }

  changeTown(event:any){  
    this.ilceler.map(ilce=>{
      if(event.target.value == ilce.id){
        this.editedAddress.ilce_adi  = ilce.name;
      } 
    }) 
  }

  isExistTag(event:any){ 
    this.checking = true;
    let elm =(<HTMLInputElement>event.currentTarget);

    if(event.keyCode == 32){ 
      elm.value = this.Cevir(elm.value)
      this.checking   = false;
      return false;
    }
    

    if(elm.value.length < 3) {
      this.checking   = false;
      this.tag_exist  = 1;
      return;
    }

    elm.value = this.Cevir(elm.value)
    this.accountService.isExistTag(elm.value).then((res)=>{ 
      this.tag_exist  = res?1:0  
      this.checking   = false;
    })
  }

  Cevir(text:string){
    var trMap = {
                    'çÇ':'c',
                    'ğĞ':'g',
                    'şŞ':'s',
                    'üÜ':'u',
                    'ıİ':'i',
                    'öÖ':'o'
                };
    for(var key in trMap) {
        text = text.replace(new RegExp('['+key+']','g'), trMap[key]);
    }
    return  text.replace(/[^-a-zA-Z0-9\s]+/ig, '') 
                .replace(/\s/gi, "-") 
                .replace(/[-]+/gi, "-") 
                .toLowerCase();
  }

  async save(){
    this.saveAccount() 
  }

  async saveAccount(){
    this.account.id = this.authService.USER.uid
    const loading = await this.loadingController.create({ 
      message: 'Please wait...',
      duration: 5000
    });    
    await loading.present(); 
    const prflData = { displayName: this.account.displayName, photoURL: this.account.photoURL}
    this.authService.USER.updateProfile(prflData)
    let a          = this.accountService.saveAccount(this.account)
    a.then(response=>{
      debugger
      response.subscribe(async res=>{ 
        await loading.dismiss();
        if (res['result'] == 'OK'){
          if(res['id']){ 
            this.tag_exist  = 2;
          }
          this.router.navigate(['home'])
          return
        }else if(res['result']=="Error"){
          alert("Kayıt gerçekleşmedi"+res['message']) 
        }
      })
    })
  }

  selectPhoto(){
    let BU = this;  
    setTimeout(()=>{
      this.imgInputElm       = document.createElement('input');
      this.imgInputElm.type  = "file";
      this.imgInputElm.setAttribute("accept","image/*");  
      this.imgInputElm.addEventListener('change',(event)=>{ 
        BU.imageChangedEvent  = event; 
        if(event.currentTarget['files'].length) this.agCropperClose   = false;
        
      })
      this.imgInputElm.click();
    },1)
  }

  back(){
    this.location.back();
  }

  //----------------------------------------------      I m a g e C r o p     --------
  async kirp(){ 
    if(!this.account.id){
      alert("Hesaba erişilemedi !")
      return;
    }
    this.imageChangedEvent  = false;
    this.agCropperClose     = true; 
    let blob                = this.fileService.base64ToBlob(this.profilPhotoBase64) 
    let file                = new File([blob],'deneme');
    let prflBlob            = await this.fileService.resizeImage(file,512,512,.8)
    let filePath            = 'profile/' + this.account.id;
    firebase.storage().ref(filePath).put(prflBlob).then((upload)=>{
      upload.ref.getDownloadURL().then((url)=>{ 
        this.account.photoURL = url
      }).catch((err)=>{
        console.log("selectPhoto : Resim storage de bulunamadı" , err.messages) 
      })
    })
  }

  imageChangedEvent : any        = ''; 
  agCropperClose    : boolean    = true; 
  imageCropped(event: ImageCroppedEvent) { 
      this.agCropperClose     = false
      this.profilPhotoBase64    = event.base64;  
  }

  imageLoaded() {
      // show cropper
  }

  cropperReady() {
      // cropper ready
  }

  loadImageFailed() {
      // show message
  }

}